//Navbar
.navbar {
	background-color: @darkblue;
	margin: 5;
	min-width: 330px;
	z-index:2;
}
.navbar-offcanvas {
	background-color: #f4f4f4;
	z-index:1;
	
}
.navbar-header {
	a {
		color: @defaultcolor;
		text-decoration: none;
	}
}
.navbar-brandd {
	font-size: 50px;
}
.navbar-form {
	margin-top: 20px;
}
#menus {
	background-color: #f4f4f4;
}

.menulink {
	>li {
		>a {
			text-decoration: none;
			color: @black;
			padding: 0;
			margin: 15px;
			&:hover {
			    cursor:pointer;
				text-decoration: none;
				color: @red;
			}
		}
	}
}
.form-group {
	position: relative;
}
.fa-search {
	position: absolute;
	padding: 10px;
	pointer-events: none;
}