@defaultcolor:rgba(0, 0, 0, 0);
@duration:.3s;
.shadow-radial {
	display: inline-block;
	position: relative;
	box-shadow: 0 0 1px @defaultcolor;
	transform: translateZ(0);
	-webkit-tap-highlight-color: @defaultcolor;
	&:before {
		pointer-events: none;
		position: absolute;
		content: '';
		left: 0;
		width: 100%;
		box-sizing: border-box;
		background-repeat: no-repeat;
		height: 5px;
		opacity: 0;
		transition-duration: @duration;
		transition-property: opacity;
		bottom: 100%;
		background: radial-gradient(ellipse at 50% 150%, rgba(0, 0, 0, .6) 0%, @defaultcolor 80%);
	}
	&:after {
		pointer-events: none;
		position: absolute;
		content: '';
		left: 0;
		width: 100%;
		box-sizing: border-box;
		background-repeat: no-repeat;
		height: 5px;
		opacity: 0;
		transition-duration: @duration;
		transition-property: opacity;
		top: 100%;
		background: radial-gradient(ellipse at 50% -50%, rgba(0, 0, 0, .6) 0%, @defaultcolor 80%);
	}
	&:hover {
		&:before {
			opacity: 1;
		}
		&:after {
			opacity: 1;
		}
	}
}
.grow {
	display: inline-block;
	transition-duration: @duration;
	transition-property: transform;
	box-shadow: 0 0 1px @defaultcolor;
	transform: translateZ(0);
	-webkit-tap-highlight-color: @defaultcolor;
	&:hover {
		transform: scale(1.1);
	}
}