//Breakpoints
@media only screen and (max-width : 767px) {
	li {
		float: left;
		clear: both;
		line-height: 50px;
	}
	.navbar-brandd {
		margin: 20px;
		font-size: 40px;
	}
	.navbar-offcanvas {
		padding-top: 150px;
	}
	#menu {
		>.menulink {
			>li {
				>a {
					left: 0;
					padding-bottom: 4px;
					margin: 0;
					font-size: 11px;
					text-decoration: none;
					font-weight:bolder;
				}
			}
			>a {
				&:hover {
					text-decoration: none;
					color: @red;
				}
			}
		}
	}
	.border1 {
		border: 2px solid red;
		width: 100px;
	}
	header {
		padding-top: 150px;
	}
	#calendar{
	    display:none;
	}
	.fa-4x{
    	margin-right:25px;
    }
}

@media only screen and (min-width : 768px) {
	#menu {
		>.menulink {
			>li {
				>a {
					font-size: 9.5px;
					font-weight: bolder;
				}
			}
		}
	}
	#home {
		display: none;
	}
}
@media (min-width: 830px) {
	#menu {
		>.menulink {
			>li {
				>a {
					font-size: 10px;
				}
			}
		}
	}
}
@media only screen and (min-width : 992px) {
	#menu {
		>.menulink {
			>li {
				>a {
					font-size: 15px;
				}
			}
		}
	}
}
@media only screen and (min-width : 1200px){
	#menu {
		>.menulink {
			>li {
				>a {
					font-size: 18px;
				}
			}
		}
	}

}
