//Content
blockquote {
    &:hover{
        color:@red;
        }
	a {
		&:hover {
			color: @red;
		}
	}
}
#calendar {
	margin-top: 10px;
	margin-left: 10px;
	font-size: 12px;
}
.modal-body {
	color: #a8a8a8a;
	font-weight: bolder;
}
.left-addon {
	.glyphicon {
		left: 0px;
	}
	input {
		padding-left: 30px;
	}
}
.clearfix {
	clear: both;
}
li {
	list-style-type: none;
	color: @black;
	float: left;
}
ol>li{
    list-style-type:disc;
    
}
#lowerhr {
	margin: 0;
	padding: 0;
}

.carousel-indicators {
	display: none;
}
.col-md-5{
	>a{
		>.fa-facebook{color:#3b5998;}
		>.fa-twitter{color:skyblue;}
		>.fa-instagram{color:#8a3ab9;}
		>.fa-university{color:#e95950;}
		&:hover{
			opacity:.8;
		}
	}
	
}
.col-md-2{
	font-weight:bolder;
	.lead{
		font-weight:bolder;
	}
}
.col-sm-2{
	font-weight:bolder;
	.lead{
		font-weight:bolder;
	}
}

video{
	width:100%;
}
.fa-4x{
	margin-right:40px;
}
#article{
    color:@red;    
}
.breadcrumb {
	background: rgba(245, 245, 245, 0.04);
	border: 0px solid rgba(245, 245, 245, 0.26);
	border-radius: 4px;
	display: block;
	margin-bottom:0px;
	li {
		font-size: 14px;
		float:none;
	}
	a {
		color: @color_1;
		&:hover {
			color: @color_2;
		}
	}
	>.active {
		color: @color_3;
	}
	>li&+li {
		&:before {
			color: @color_4;
			content: "\002F\00a0";
		}
	}
}
.external-article{
    font-size:18px;
}
