//Footer
footer {
	position: absolute;
	background-color: @darkblue;
	width: 100%;
	color: #5b93e6;
	.container-fluid {
		.col-md-4 {
			>img {
				margin: 20px;
				width: 150px;
				height: 125px;
				float: left;
				opacity: .8;
			}
			>ul {
				>h4 {
					text-align: center;
				}
				li {
					&:first-child {
						margin-top: 20px;
					}
				}
			}
			text-align: center;
			ul {
				h4 {
					>.last {
						opacity: .5;
					}
				}
			}
		}
	}
	>.container-fluid {
		>.copyimage {
			height: 10px;
			width: 10px;
		}
	}
	a {
		&:hover {
			color: #69cfd8;
		}
	}
}